@import "../settings";
@import "../mixins";
@import "../fonts/roboto";
@import "../fonts/steelfish";



.roboto {
font-family: RobotoSlabRegular, Georgia, serif;
	i, em {
	font-style: italic;
	font-weight: inherit;
	font-size: inherit;
	}
	b, strong {
	font-style: inherit;
	font-weight: 700;
	font-size: inherit;
	font-family: RobotoSlabBold, Georgia, serif;
	}
	&.xbold {
	font-style: inherit;
	font-weight: 800;
	font-size: inherit;
	font-family: RobotoSlabExtrabold, Georgia, serif;
	};
	&.black {
	font-style: inherit;
	font-weight: 900;
	font-size: inherit;
	font-family: RobotoSlabBlack, Georgia, serif;
	}
}


.steelfish {
 font-weight: 400;
font-family: steelfish, "Arial Narrow", Arial, san-serif !important;
	i, em {
	font-style: italic;
	font-weight: inherit;
	font-size: inherit;
	}
	b, strong {
	font-style: inherit;
	font-weight: 700 !important;
	font-size: inherit;
	font-family: steelfish-bold,  "Arial Narrow", Arial, sans-serif !important;
	
	}
	&.extrabold {
	font-weight: 800 !important;
	font-family: steelfish-extrabold, "Arial Black", Arial, sans-serif !important;
	}
}

